import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { EventRespRuleBean } from '../../bean/event-resp-rule.bean';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { InputTextModule, Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';

@Component({
  selector: 'ec-resp-rule-modify',
  templateUrl: './ec-resp-rule-modify.component.html',
  styleUrls: ['./ec-resp-rule-modify.component.scss'],
  providers: [Commfunc]
})
export class EcRespRuleModifyComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  userform: FormGroup; //校验
  // bean
  eventRespRuleBean: EventRespRuleBean = new EventRespRuleBean();
  //响应类型 是否通知客户
  istzkh: boolean = true;
  //响应类型 是否周期性提醒
  istz_Zqxtx: boolean = false;
  // 执行服务名称
  execServiceName: string = '';
  taskCategoryName: string = '';
  // 消息模板
  messageStencilName: string = '';

  msgs: Message[] = [];
  constructor(
    private commonFunc: Customtools,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.userform = fb.group({
      responseType: ['', Validators.required],
      execFunction: ['', Validators.required],
      execService: ['', Validators.required],
      remindCycle: ['', Validators.required],
      messageContent: ['', Validators.required],
      messageStencilId: ['', Validators.required],
      isDayRemind: ['', Validators.required],
      isLaterRemind: ['', Validators.required],
      remindIntervalType: ['', Validators.required],
      remindCount: ['', Validators.required],
      repeatCycleType: ['', Validators.required],
      sendChannel: ['', Validators.required],      
      repeatCycleDay: ['', Validators.required],
      remindIntervalMin: ['', Validators.required],
    })
  }
  ngOnInit() {
    this.codeValue.codeValues(); //调用方法，获取全部码值
    this.codeValue.responseTypeOptions = this.codeValue.code['ecresponseTypeOptions'];
    // console.log(this.inValue);
    this.eventRespRuleBean = this.inValue;
    if (this.inValue.responseType == '1') {
      this.istzkh = false;
      this.execServiceName = this.inValue.resServiceTemplateShow;
    } else {
      this.messageStencilName = this.inValue.resServiceTemplateShow;
      this.responseTypeChange();
      
    }
  }

  // 保存/提交
  modifySaveBtn() {
    //如果响应类型是通知客户
    if ('2' == this.eventRespRuleBean.responseType || '3' == this.eventRespRuleBean.responseType) {
      if (this.istz_Zqxtx) {
        //提醒次数不能为空
        if (!this.eventRespRuleBean.remindCount) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '提醒次数不能为空' });
          return;
        }
      }
      //消息模板，消息内容  不能同时 为空
      if (!this.eventRespRuleBean.messageStencilId && !this.eventRespRuleBean.messageContent) {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: '消息模板和消息内容不能同时为空' });
        return;
      }
      if (this.eventRespRuleBean.messageStencilId && this.eventRespRuleBean.messageContent) {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: '消息模板和消息内容不能同时填写' });
        return;
      }
      this.eventRespRuleBean.resServiceTemplateShow = this.messageStencilName;
      this.eventRespRuleBean.resMethodContentShow = this.eventRespRuleBean.messageContent;
    } else if (this.eventRespRuleBean.responseType == '1') {
      
      // if (!this.execServiceName && !this.eventRespRuleBean.execFunction) {
      //   this.msgs = [];
      //   this.msgs.push({ severity: 'info', summary: '提示', detail: '执行服务和执行方法不能同时为空' });
      //   return;
      // }

      if (!this.execServiceName) {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: '执行服务不能为空!' });
        return;
      }

      if (!this.eventRespRuleBean.execService) {
          this.msgs = [];
          this.msgs.push({ severity: 'info', summary: '提示', detail: '执行服务不能为空!' });
          return;
      }

      // if (this.execServiceName && this.eventRespRuleBean.execFunction) {
      //   this.msgs = [];
      //   this.msgs.push({ severity: 'info', summary: '提示', detail: '执行服务和执行方法不能同时填写' });
      //   return;
      // }

      this.eventRespRuleBean.resServiceTemplateShow = this.taskCategoryName;
      this.eventRespRuleBean.resMethodContentShow = this.eventRespRuleBean.execFunction;
      this.eventRespRuleBean.execServiceName = this.taskCategoryName;
    }
    this.outValue.emit(this.eventRespRuleBean);
  }

  // 响应类型
  responseTypeChange() {
    //如果响应类型是 通知客户
    if ('2' == this.eventRespRuleBean.responseType || '3' == this.eventRespRuleBean.responseType) {
      this.istzkh = true;
      //是否周期性提醒
      if ('2' == this.eventRespRuleBean.remindCycle) {
        this.istz_Zqxtx = true;
      } else {
        this.istz_Zqxtx = false;
      }
    } else {
      this.istzkh = false;
      this.istz_Zqxtx = false;
    }
  }

   // 发送渠道
   sendChannelChange(item) {
    debugger;
    for (let i = 0; i < this.codeValue.sendChannelOptions.length; i++) {
        if (item.value == this.codeValue.sendChannelOptions[i].value) {
          this.eventRespRuleBean.sendChannelShow = this.codeValue.sendChannelOptions[i].label;
          break
        }
    }

}

  // 提醒周期
  remindCycleChange() {
    //是否周期性提醒
    if ('2' == this.eventRespRuleBean.remindCycle) {
      this.istz_Zqxtx = true;
    } else {
      this.istz_Zqxtx = false;
    }
  }

  //返回
  modifyCloseBtn() {
    let parm = {
      display: false,
      code: 'cancelModel'
    }
    this.outValue.emit(parm);
  }


  // 模态窗管理
  dialogConfigure = {
    parent: this,
    selectData: '',
    displayMsg: false,
    displayExcService: false,
    eventRespRuleBean: this.eventRespRuleBean,
    // 消息模板查询弹框
    msgTempShow() {
      this.displayMsg = true;
    },
    // 执行服务 查询弹框
    execServiceShow() {
      this.displayExcService = true;
      this.selectData = this.eventRespRuleBean.execService;
    },
    // 服务选择回调
    getSelectedService(event) {
      if (event.code === 'save') {
        this.displayExcService = false;
        this.parent.execServiceName = event.taskCategoryName;
        this.eventRespRuleBean.execService = event.taskCategoryId;
        this.parent.taskCategoryName = event.taskCategoryName;
      } else {
        this.displayExcService = false;
      }
    },
    //消息 短信的回显模板名称
    noteValue(event) {
      if (!event) {
        
      } else {
        this.eventRespRuleBean.messageStencilId = event[0].templateId;
        this.parent.messageStencilName = event[0].templateName;
      }
      this.displayMsg = false;
    },
    // 关闭自定义设置
    closeUserDefinedSetDispaly() {
      this.displayExcService = false;
    }
  }


  // 码值
  codeValue = {
    commfunc: this.commfunc,
    code: '',
    // 响应类型
    responseTypeOptions: [],
    // 发送渠道
    sendChannelOptions: [
      { label: '24小时营业厅', value: '1' },
      { label: '移动工作站', value: '2' },
      { label: '移动审批', value: '3' },
      { label: '短信', value: '4' },
      { label: '微信', value: '5' },
      { label: '小微平台', value: '6' }
    ],
    // 提醒周期
    remindCycleOptions: [
      { label: '单次提醒', value: '1' },
      { label: '周期性提醒', value: '2' }
    ],
    // 是否 
    whetherOptions: [
      { label: '否', value: '0' },
      { label: '是', value: '1' }
    ],
    // 提醒间隔类型
    remindIntervalTypeOptions: [
      { label: '否', value: '0' },
      { label: '是', value: '1' }
    ],
    // 重复周期
    repeatCycleTypeOptions: [
      { label: '从不', value: '1' },
      { label: '每天', value: '2' },
      { label: '每周', value: '3' },
      { label: '每月', value: '4' },
      { label: '每年', value: '5' },
      { label: '每个工作日', value: '6' },
      { label: '每个节假日', value: '7' },
      { label: '自定义周期', value: '8' },
      { label: '一直提醒', value: '9' }
    ],
    // 码值
    codeValues() {
      this.commfunc.codeValue();
      let ret = this.commfunc.codeDatas;
      return this.code = ret; //code是自己声明的变量
    }
  }

}

