import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Message } from "primeng/primeng";
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';//删除
import * as $ from 'jquery';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { EventDefinitionBean } from '../bean/event-definition.bean';
import { EventRespRuleBean } from '../bean/event-resp-rule.bean';
import * as moment from 'moment';
import * as _ from 'lodash';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from '../../../../../../@uisftech/common/service/http.constant';
/**
 * 触发规则
 */
@Component({
	selector: 'ecenter-resp',
	templateUrl: './ecenter-resp.component.html',
	styleUrls: ['./ecenter-resp.component.scss'],
	providers: [CusOperationHttpService, ConfirmationService]
})
export class EcenterRespComponent implements OnInit {
	@Output() outValue: EventEmitter<any> = new EventEmitter();
	ch: any = CALENDAR_CN;
	tabMesg: string = tableMessage;
	showFlag: boolean = true;

	ruleList: any = [];  //响应规则集合

	ruleListTemp = new Array();
	msgs: Message[] = [];
	code: any;
	inValue2: any;
	eventDefinitionBean: EventDefinitionBean = new EventDefinitionBean();
	flagCopy: string;
	show: boolean = false;
	activiteData: any = {};//接收详情的数据
	orgId: string;
	userId: string;

	showDisabled: boolean = false;//可编辑-修改为已发布进行中时，只能修改
	statusId: string;
	activeIndex: number = 1;
	value: any;//div
	items: any = [];
	basicElement: string;

	//模态框
	showModel: string;
	displayAdd: boolean = false;
	displayModify: boolean = false;
	//模态框的标题
	headerTitle: string;
	first: number = 0;//分页控制
	//修改的参数
	updateValue: any = [];
	// 保存状态
	status: any = '';
	selectData: any = {};

	//响应规则操作类型 1:新增 2: 修改
	rule_operflag: string = "1";


	//操作类型 1:新增 2: 修改
	operflag: string = "1";
	// 是否上一步下一步  0 不是  1 新增的是
	stepflag: string = "1";
	//响应类型
	responseTypeOptions = [];
	// 发送渠道
	sendChannelOptions = [
		{ label: '24小时营业厅', value: '1' },
		{ label: '移动工作站', value: '2' },
		{ label: '移动审批', value: '3' },
		{ label: '短信', value: '4' },
		{ label: '微信', value: '5' },
		{ label: '小微平台', value: '6' }
	];
	constructor(
		public router: Router,
		public routeInfo: ActivatedRoute,
		public cusOperationHttpService: CusOperationHttpService,
		public confirmationService: ConfirmationService,
		public commfunc: Commfunc
	) {
		this.codeValues() //调用方法，获取全部码值
		this.responseTypeOptions = this.code['ecresponseTypeOptions'];

	}

	ngOnInit() {
		debugger;
		this.status = '';
		this.operflag = this.commfunc.getSessionDataCH('operflag');
		this.stepflag = this.commfunc.getSessionDataCH('stepflag');
		this.eventDefinitionBean = JSON.parse(this.commfunc.getSessionDataCH('event_eventDefinitionBean'));

		//如果是新增，并且不是上一步下一步
		if (this.operflag == '1') {
			//不是上一步下一步
			if (this.stepflag == '0') {
				this.ruleList = [];
			} else {
				// 是上一步下一步
				if (this.stepflag == '1') {
					this.ruleList = this.eventDefinitionBean.eventResponseRuleList;
				}
			}

		} else {
			//如果是修改
			if (this.operflag == '2') {

				//是上一步下一步
				if (this.stepflag == '1') {

					this.ruleList = this.eventDefinitionBean.eventResponseRuleList;

				} else {
					let param: EventDefinitionBean = new EventDefinitionBean();

					param.eventId = this.eventDefinitionBean.eventId;

					this.queryEventResponseRuleList(param);
				}

			}
		}



		this.activeIndex = 3;
		this.value = "3";

		this.commfunc.setSessionDataCH('index', 3);


		let flagIndex = JSON.parse(this.commfunc.getSessionDataCH('flagIndex'));
		if (flagIndex) {
			this.commfunc.clearSession('flagIndex');
			if (this.commfunc.getSessionDataCH('basicElement')) {

			}
		} else {
			if (this.commfunc.getSessionDataCH('basicElement')) {
				this.commfunc.clearSession('basicElement');
			}
		}


		// cx 步骤条样式
		setTimeout(() => {
			let steps = document.getElementById('compId');
			let stepStyle = steps.getElementsByClassName('ui-steps-number');
			for (let i = 0; i < stepStyle.length; i++) {
				stepStyle[i]['style'].backgroundColor = '#757575';
				if (i < this.activeIndex) {
					stepStyle[i]['style'].backgroundColor = '#66CC00';
				}
				if (i == this.activeIndex) {
					stepStyle[i]['style'].backgroundColor = '#ffc107';
				}
			}
		}, 200);

	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}


	last() {

		this.commfunc.setSessionDataCH('operflag', this.operflag);
		this.commfunc.setSessionDataCH('stepflag', '1');
		this.eventDefinitionBean.eventResponseRuleList = this.ruleList;
		this.commfunc.setSessionDataCH('event_eventDefinitionBean', JSON.stringify(this.eventDefinitionBean));

		this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/create-eventcenter-activite/ecenter-ensure']);
	}

	returnBtn() {

		this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/eventcenter-activity-query']);
	}



	//新增
	add() {
		this.displayAdd = true;
		this.headerTitle = '新增';
		this.commfunc.setSessionDataCH('rule_operflag', "1");

	}

	//新增 修改 之后 的刷新
	refreshAddEdit() {

		this.ruleList = [];

		let length = this.ruleListTemp.length;

		if (this.ruleListTemp) {

			this.ruleList = new Array(length);

			for (var i = 0; i < length; i++) {

				this.ruleList[i] = this.ruleListTemp[i]
			}
		}

	}

	//删除 之后 的刷新
	refreshDelete() {

		this.ruleListTemp = [];

		let length = this.ruleList.length;

		if (this.ruleList) {

			this.ruleListTemp = [];

			for (var i = 0; i < length; i++) {

				this.ruleListTemp.push(this.ruleList[i]);
			}
		}

	}


	//新增的回调
	dialogCall(event) {
		if (!event) {
			this.displayAdd = false;
			return;
		}
		this.ruleListTemp.push(event);
		this.refreshAddEdit();
		this.displayAdd = false;
	}

	//修改的回调
	dialogEditCall(event) {
		if (!event) {
			this.displayModify = false;
			return;
		}
		if (this.ruleList) {
			for (var i = 0; i < this.ruleList.length; i++) {
				if (this.isTheOne(event, this.ruleList[i])) {
					this.ruleList[i] = event;
					break;
				}
			}
		}
		this.displayModify = false;
	}

	// 修改按钮
	modifyClick(item) {
		console.log(item);
		this.displayModify = true;
		this.headerTitle = '修改';
		this.updateValue = item.channelId;
		this.selectData = item;
		this.commfunc.setSessionDataCH('rule_operflag', '2');


	}

	isTheOne(deOne: EventRespRuleBean, deTwo: EventRespRuleBean) {
		if (deOne.randomNum == deTwo.randomNum) {
			return true;
		}
		return false;
	}

	//删除按钮事件
	deleteClick(deleteitem: EventRespRuleBean) {
		debugger;
		this.confirmationService.confirm({
			message: '确认要删除吗?',
			accept: () => {
				//条件
				debugger;

				this.ruleList = [];

				let randomNum = deleteitem.randomNum;


				let length = this.ruleListTemp.length;

				if (this.ruleListTemp) {

					this.ruleList = [];

					for (var i = 0; i < length; i++) {

						if (this.isTheOne(deleteitem, this.ruleListTemp[i])) {
						} else {
							this.ruleList.push(this.ruleListTemp[i]);
						}

					}
				}

				this.refreshDelete();
			}
		});
	}



	// 完成
	finish() {
		//操作人
		let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
		this.eventDefinitionBean.createdPersonId = commonHeader.userId;

		this.eventDefinitionBean.createdPersonOrg = commonHeader.orgId;

		let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
		let userName = chName['userName'];//机构名

		this.eventDefinitionBean.createdPersonName = userName;

		//来源渠道, 默认小微智慧金融服务平台 1：24小时营业厅 2：小微智慧金融服务平台 3：移动工作站 4:移动审批 5:短信6:微信
		this.eventDefinitionBean.sourceChannel = '2';

		//来源渠道 ，默认 枢纽推送 1:客户自定义 2：用户自定义 3：周期性行为 4：短信抓取（我行） 5：短信抓取（他行） 6：枢纽推送 7：其他
		this.eventDefinitionBean.eventSource = '6';

		//响应规则集合
		this.eventDefinitionBean.eventResponseRuleList = this.ruleList;

		let param: EventDefinitionBean = this.eventDefinitionBean;

		if (param.eventStartTime) {
			param.eventStartTime = this.commfunc.transDateN(param.eventStartTime);//事件开始时间

		}

		if (param.eventEndTime) {
			param.eventEndTime = this.commfunc.transDateN(param.eventEndTime);//事件结束时间
		}

		debugger;

		//新增
		if ('1' == this.operflag) {
			this.cusOperationHttpService.createEventDefinitionInfo(param).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.msgs = [];
					this.msgs.push({ severity: 'success', summary: '提示：', detail: '保存成功' });
					setTimeout(() => {
						//保存成功，跳转到 查询界面
						this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/eventcenter-activity-query']);
					}, 500);
				} else {
					this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
					return;
				}
			});
		}

		// 修改
		if ('2' == this.operflag) {
			this.cusOperationHttpService.updateEvDefByEvId(param).subscribe(data => {
				if (data.returnCode.code == 'success') {
					this.msgs = [];
					this.msgs.push({ severity: 'success', summary: '提示：', detail: '修改成功' });
					setTimeout(() => {
						//保存成功，跳转到 查询界面
						this.router.navigate(['/pages/tzb/custom/eventcenter/eventcenter-activite/eventcenter-activity-query']);
					}, 500);
				} else {
					this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
					return;
				}
			});
		}
	}

	//点击X，关闭模态框
	onHideClose() {
		document.body.style.overflow = 'auto';
		this.displayAdd = false;
		this.displayModify = false;
	}

	// 事件规则明细查询
	queryEventResponseRuleList(param: any) {
		this.cusOperationHttpService.queryEventResponseRuleList(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				data.eventResponseRuleList.forEach((item, index) => {
					//	item['sendChannelShow'] = '小微智慧金融服务平台';
					if (item.responseType == '1') {
						item['resServiceTemplateShow'] = item.execServiceName;
						item['resMethodContentShow'] = item.execFunction;
					} else if (item.responseType == '2' || item.responseType == '3') {
						item['resServiceTemplateShow'] = item.messageStencilName;
						item['resMethodContentShow'] = item.messageContent;
					}
				});
				console.log(data.eventResponseRuleList);
				this.ruleList = data.eventResponseRuleList;

			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		});
	}

}
